<template>
  <div>
    <div class="bg">
      <v-app id="inspire">
        <v-app-bar
          color="grey lighten-5"
          dark
          app
          class="default-layout__navbar"
          extended
          extension-height="3"
        >
          <div class="d-flex align-center">
            <v-img
              alt="YNOTCOLLAB Logo"
              class="shrink mr-2"
              contain
              src="../assets/Logo-gradient.svg"
              transition="scale-transition"
              width="230"
            />
          </div>

          <v-spacer></v-spacer>

          <v-btn color="purple" to="/home" dark>DEMO</v-btn>
        </v-app-bar >

        <v-container id="signinup-form" class="fill-height">
          <v-row align="center" justify="center" no-gutters>
            <v-col cols="12" sm="8">
              <v-card class="evelation-12 card">
                <v-window v-model="step">
                  <v-window-item :value="1">
                    <v-row class="fill-height">
                      <v-col cols="12" md="8" class="pt-6 pb-6">
                        <v-card-title class="text-center" primary-title>
                          <span class="headline">{{ currentTitle }}</span>
                        </v-card-title>
                        <v-card-text>
                          <v-text-field label="Email"></v-text-field>
                          <v-text-field
                            label="Password"
                            type="password"
                          ></v-text-field>
                          <v-btn to="/home" dark color="purple">LogIn</v-btn>
                          <span class="ml-3">Or Sign In with Google!</span>
                          <v-icon class="mdi mdi-google"></v-icon>
                        </v-card-text>
                      </v-col>
                      <v-col cols="12" md="4">
                        <div>
                          <v-card-text>
                            <h3 class="text-center headline mb-3">
                              Not a User?
                            </h3>
                            <h5 class="text-center overline">
                              Create an account here!
                            </h5>
                          </v-card-text>
                          <div class="text-center mb-6">
                            <v-btn color="purple" outlined @click="step = 2"
                              >Sign Up</v-btn
                            >
                          </div>
                        </div>
                      </v-col>
                    </v-row>
                  </v-window-item>

                  <v-window-item :value="2">
                    <v-row class="fill-height">
                      <v-col cols="12" md="8" class="pt-6 pb-6">
                        <v-card-title class="text-center" primary-title>
                          <span class="headline">{{ currentTitle }}</span>
                        </v-card-title>
                        <v-card-text>
                          <v-text-field
                            label="Email"
                            type="text"
                          ></v-text-field>
                          <v-text-field
                            label="Username"
                            type="text"
                          ></v-text-field>
                          <v-text-field
                            label="Password"
                            type="password"
                          ></v-text-field>
                          <v-radio-group v-model="row" row>
                            <label class="mr-2">Sign up as</label>
                            <v-radio
                              label="Projectfinder"
                              value="radio-1"
                            ></v-radio>
                            <v-radio
                              label="Projectowner"
                              value="radio-2"
                            ></v-radio>
                          </v-radio-group>
                          <span class="caption purple--text text--darken-1">
                            This cannot be changed
                          </span>
                          <br>
                          <v-btn to="/home" dark color="purple" class="mt-2">Create Account</v-btn>
                        </v-card-text>
                      
                      </v-col>
                      <v-col cols="12" md="4">
                        <div>
                          <v-card-text>
                            <h1 class="text-center headline mb-3">
                            Already an User?
                            </h1>
                            <h5 class="text-center overline">
                              Sign In here!
                            </h5>
                          </v-card-text>
                          <div class="text-center mb-6">
                            <v-btn color="purple" outlined @click="step = 1"
                              >Sign In</v-btn
                            >
                          </div>
                        </div>
                      </v-col>
                    </v-row>
                  </v-window-item>
                </v-window>
              </v-card>
            </v-col>
          </v-row>
        </v-container>
<v-footer
    dark
    padless
  >
    <v-card
      class="flex"
      flat
      tile
    >
      <v-card-title class="purple">
        <p class="">Get connected with us on social networks!</p>

        <v-spacer></v-spacer>

        <v-btn
          v-for="icon in icons"
          :key="icon"
          class="mx-4"
          dark
          icon
        >
          <v-icon size="24px">
            {{ icon }}
          </v-icon>
        </v-btn>
      </v-card-title>

      <v-card-text class="py-2 white--text text-center">
        {{ new Date().getFullYear() }} — <strong>Vuetify</strong>
      </v-card-text>
    </v-card>
  </v-footer>
       
      </v-app>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    step: 1,
    icons: [
        'mdi-facebook',
        'mdi-twitter',
        'mdi-linkedin',
        'mdi-instagram',
      ],
  }),
  computed: {
    currentTitle() {
      switch (this.step) {
        case 1:
          return 'Sign In';
        case 2:
          return 'Sign Up';
        default:
          return 'Sign In';
      }
    },
  },
};
</script>

<style lang="scss" scoped>


</style>
